.resume-container{
    background-color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: fit-content;
 
    font-size: 16px;
    padding-bottom: 150px;
    padding-top: 130px;
}

.resume-card{
    display: flex;
    height: 360px;
    width: 1000px;
 
}

.resume-bullets{
    height: 100%;
    box-shadow: 15px 0 9px -15px #1f2235;
    width: 320px;
}

.resume-bullet-details{
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
}

.experience-description{
    margin: 10px 0 0 0;
    text-align: justify;
    max-width: 100%;
}

.resume-details-carousal{
    transition: transform 1s ease-out;
}


.bullet-container{
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
}

.bullets{
    width: 80%;
    position: relative;
    z-index: 2;
}

.bullet{
    display: flex;
    align-items: center;
    background-color: #1f2235;
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
    cursor: pointer;
}


.bullet-label{
    font-size: 14px;
    white-space: wrap;
    font-family: "Poppins SemiBold";
}

.bullet-logo{

    margin: 0 30px 0 0;
    background-color: aliceblue;
}

.bullet-icons{
    width: 40px;
    height: 100%;
    z-index: 1;
    background-color: #1f2235;
    position: absolute;
    color: aliceblue;
}

.selected-bullet{
    color: white;
    width: 100%;
}

.resume-screen-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 360px;
    width: 100%;
}


.resume-heading{
    display: flex;
    flex-direction: column;
}


.resume-main-heading{
    position: relative;
 display: flex;
   justify-content: space-between;
}

.resume-main-heading span{
    color: var(--dark-orange);
    font-size: 16px;
    font-family: "Poppins SemiBold";
}

.heading-bullet{
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    border-radius: 50%;
    background-color: #ff5823
}

.resume-sub-heading{
   
   font-size: 14px;
    font-family: "Poppins SemiBold";
    color: var(--black);
    margin: 10px 0 0;
 
}

.resume-heading-description{
    font-size: 12px;
    text-align: justify;
}

.heading-date{
    background-color: var(--dark-orange);
    padding: 4px 12px; 
    font-size: 14px;
    border-radius: 14px;
    color: white;
}


.skill-parent{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
}

.programming-skills-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}


.skill-percentage{
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--dark-orange);
}


.skill-percentage .active-percentage{
 height: 15px;
 transition: 0.8s ease;
 position: absolute;
 background-color: #1f2235;
}

.skill-parent span{
    font-family: "PoppinsSemiBold";
    font-size: 16px;
    color: var(--dark-orange);
}

@media only screen and (max-width: 1110px){
     .resume-card{
        width: 800px;

     }
     .resume-bullet-details{
        padding: 0 0 0 70px;
     }
}

@media only screen and (max-width: 1023px){
    .resume-content{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .bullets{
        width: 90%;
    }
    .resume-card{
        height: fit-content;
        display: flex;
        flex-direction: column;
        margin: 30px 0;
        align-items: center;
        width: 90%;
    }
    .resume-bullet-details{
        height: 360px;
        width: fit-content;
        padding:0 ;
    }
    .resume-bullets{
        width: 100%;
        margin: 30px 0;
    }
    .selected-bullet{
        width: 100%;
    }
}